.post {
	@include clearfix();
	margin: 0 auto;
	position: relative;
	z-index: 2;

	&__content {
		@include clearfix();
		@include font-size(16);
		margin: 20px 0 20px 20px;
		max-width: 1200px;
		position: relative;
		z-index: 2;

		@include mq($bp-tablet) {
			@include font-size(18);
			margin: 40px 30px 80px 50px;
		}

		@include mq($bp-desktop, true) {
			@include font-size(20);
			margin: 40px 30px 120px 50px;
		}
	}
}

// VIDEO
.video {
	clear: both;
	display: block;
	float: left;
	margin: 0;
	width: 100%;

	&__wrapper {
		height: 0;
		margin: 0 auto;
		overflow: hidden;
		padding: 0 0 56.25%; // 16:9 ratio
		position: relative;
		width: 100%;

		// scss-lint:disable ImportantRule
		iframe,
		object,
		embed {
			height: 100% !important;
			left: 0;
			position: absolute;
			top: 0;
			width: 100% !important;
		}
		// scss-lint:enable ImportantRule
	}
}
